.page-header {
  background-image: linear-gradient(to right, rgb(253, 113, 44) 0%, rgb(242, 23, 128) 100%);
}

.page-header__wrapper {
  @include fluid-container();
}

@media ($mobile-width) {

  .page-header {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

@media ($tablet-width) {
  .page-header {
    padding-bottom: 40px;
  }
}


@media ($desktop-width) {
  .page-header {
    position: relative;

    padding-top: 30px;

    &::before {
      content: '';
      position: absolute;
      //left: -102px;
      top: 97px;
      right: 0;
      z-index: 2;

      width: 50%;
      max-width: 1920px;
      height: 774px;
      //margin-left: 50%;

      background-image: url('../img/iphones-only.png');
      background-repeat: no-repeat;
      background-position: top 49px left 0;

      transition: top $transition-time $transition-function;

      will-change: top;
    }

    &::after {
      content: '';
      position: absolute;
      //left: -102px;
      top: 97px;
      right: 0;
      z-index: 1;

      width: 50%;
      max-width: 1920px;
      height: 774px;
      //margin-left: 50%;

      background-image: url('../img/iphones-shadows-only.png');
      background-repeat: no-repeat;
      background-position: top 49px left 0;
    }
  }

  .animation-iphone-rise {
    &::before {
      top: 70px;
    }
  }
}
